@font-face {
  font-family: 'SephoraSerifDisplayDigital';
  src: url(./media/fonts/SephoraSerifDisplayDigital-Book.otf);
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'SephoraSerifDisplayDigital';
  src: url(./media/fonts/SephoraSerifDisplayDigital-Medium.otf);
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'SephoraSerifDisplayDigital';
  src: url(./media/fonts/SephoraSerifDisplayDigital-BookItalic.otf);
  font-weight: 400;
  font-style: italic;
}
@font-face {
  font-family: "SephoraScript-Editor";
  src: url(./media/fonts/SephoraScript-Editor.otf);
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "SephoraSerifDisplayDigital";
  src: url(./media/fonts/SephoraSerifDisplayDigital-Light.otf);
  font-weight: 100;
  font-style: normal;
}

* {
  touch-action: manipulation;
}
html {
  font-size: 0.275vw;
}
html, body, #main {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: 'SephoraSerifDisplayDigital', serif;
  touch-action: pan-y;
  overflow: hidden;
  overscroll-behavior-y: none;
}

/* ip6 */
@media screen and (width: 375px) {
  html {
    font-size: 0.25vw;
  }
}
/* se */
@media screen and (width: 320px) {
  html {
    font-size: 0.25vw;
  }
}